import React, { FC } from "react";
import { graphql, PageProps } from "gatsby";
import { List, SimpleGrid, Text } from "@chakra-ui/react";

import { Layout, LinkCard, Seo } from "../components";

const BlogPage: FC<PageProps<Queries.BlogPageQuery>> = ({ data }) => {
  const blogPosts = data.allMdx.nodes;

  return (
    <Layout title="Blog">
      <Seo title="Blog" />

      <SimpleGrid columns={[1, 2, 3]} spacing={4}>
        {blogPosts.map((post) => {
          return (
            <LinkCard
              key={post.id}
              to={post.fields?.slug || ""}
              title={post.frontmatter?.title || post.fields?.slug}
              footer={post.frontmatter?.date}
              image={
                post.frontmatter?.thumbnailImage?.childImageSharp?.gatsbyImageData ||
                post.frontmatter?.featuredImage?.childImageSharp?.gatsbyImageData
              }
            >
              <Text
                dangerouslySetInnerHTML={{
                  __html: post.frontmatter?.description || post.excerpt || "",
                }}
              />
            </LinkCard>
          );
        })}
      </SimpleGrid>
    </Layout>
  );
};

export default BlogPage;

export const pageQuery = graphql`
  query BlogPage {
    allMdx(sort: { frontmatter: { date: DESC } }, filter: { internal: { contentFilePath: { regex: "//content/blog/.+/" } } }) {
      nodes {
        id
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          slug
          featuredImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
          thumbnailImage {
            childImageSharp {
              gatsbyImageData(width: 300)
            }
          }
        }
      }
    }
  }
`;
